import interfaces from '../../../utils/interface.js';






export default {
	data() {
		return {
			phone: '',
			verify: '',
			password : '',
			logining: true,
		}
	},
	methods: {
		update() { //获取验证码
			var reg = /^[1][3,4,5,7,8][0-9]{9}$/;
			if (reg.test(this.phone)) {
				//获取验证码
				let tell = this.phone;
				this.request({
					url: interfaces.get_code,
					method: "POST",
					data: {
						tell
					},
					success: ((res) => {
						if (res.error_code === 0) {
							this.logining = false;
						} 
						uni.showToast({
							icon: "success",
							title: "已发送,请注意查收",
						}, 500)
					})
				});














			} else {
				uni.showToast({
					title: '号码错误',
					icon: 'none',
					duration: 500
				});
				uni.vibrate({ //手机震动
					success: function() {
						console.log('success');
					}
				});
			}
		},
		setUpdate() { //修改密码
			if (!this.password || !this.verify) {
				uni.showToast({
					title: '请输密码或验证码',
					size : 26,
					icon: 'none',
					duration: 500
				});
			}else {
				this.request({
						url: interfaces.edit_password,
						method: "POST",
						data: {
							code : this.verify,
							tell: this.phone,
							password: this.password
						},
						success: ((res) => {
							if (res.error_code == 0) {
								uni.showToast({
									icon: "success",
									title: "成功修改密码!",
								}, 500)
								uni.navigateBack();
							}else{
								uni.showToast({
									icon: "success",
									title: res.data,
								}, 500)
							}
						})
					});





















			}
		}
	}
}
